import React from 'react';
import s from "./Nav.module.css"
import {NavLink} from "react-router-dom";

const Nav = (props) => {
    return (
        <nav className={s.nav}>
            <div className={s.navItem}>
                <p><NavLink className={s.link} activeClassName={s.activeLink}
                            to="/AuthUser/ProfilePage"></NavLink></p>
            </div>
            <div className={s.navItem}>
                <p onClick={() => props.onGoToDialogsPage("")}><NavLink className={s.link}
                                                                        activeClassName={s.activeLink}
                                                                        to="/AuthUser/DialogsPage"></NavLink></p>
            </div>
            <div className={s.navItem}>
                <p><NavLink className={s.link} activeClassName={s.activeLink}
                            to="/AuthUser/FriendsPage"></NavLink></p>
            </div>
            <div className={s.navItem}>
                <p><NavLink className={s.link} activeClassName={s.activeLink} to="/AuthUser/NewsPage"></NavLink>
                </p>
            </div>
            <div className={s.navItem}>
                <p><NavLink className={s.link} activeClassName={s.activeLink}
                            to="/AuthUser/Settings"></NavLink>
                </p>
            </div>
            <div className={s.navItem}>

                <button className={s.logoutButton} onClick={() => {
                    props.onExit()
                    // window.location.reload();
                }}>
                    
                    {/*<NavLink className={`${s.link} ${s.logoutLink}`} to="/"></NavLink>*/}
                </button>

            </div>
        </nav>
    )
}

export default Nav;