
export const requiredField = (value) => {
    if (value) {
        return undefined
    }
    return "  "
}

export const checkLenghtCreator = (minLenght, maxLenght) => (value) => {
    if (value && value.length > maxLenght) {
        return `  - ${maxLenght}`
    }
    if (value && value.length < minLenght) {
        return `  - ${minLenght}`
    }
    return undefined
}